import { css, CSSResult, customElement, html, LitElement, TemplateResult } from "lit-element";

@customElement('responsive-column')
export class ResponsiveColumns extends LitElement {
  render(): TemplateResult {
    return html`
      <div class="container">
        <main>
          <slot name="main"></slot>
        </main>
        <section id="sidebar">
          <slot name="sidebar"></slot>
        </section>
      </div>
    `;
  }

  static get styles(): CSSResult {
    return css`
      .container {
        display: flex;
        width: 100%;
        height: 100%;
      }

      main {
        background-color: #eee;
        width: 75vw;
        text-align: center;
        font-size: 2rem;
      }

      #sidebar {
        background-color: #aaa;
        width: 35vw;
        text-align: center;
        font-size: 2rem;
      }
    `;
  }
}
